  async function decodeLogsArr(logsArr){
    let logsObject = {}
    let bHash
    
    logsArr.forEach(async (logs)=>{
        let txObjectValue = []
        try{
            const decodedLog = web3.eth.abi.decodeLog(
              [ 
                {"indexed":true,"internalType":"address","name":"from","type":"address"},
                {"indexed":true,"internalType":"address","name":"to","type":"address"},
                {"indexed":true,"internalType":"uint256","name":"tokenId","type":"uint256"}
              ],
              logs.data,
              logs.topics.slice(1)
            )

            //console.log('decodedLog', decodedLog)
    
            const rebuiltLogs = {
                blockHash : logs.blockHash,
                txHash : logs.transactionHash,
                contractAddress : logs.address,
                from : decodedLog.from,
                to: decodedLog.to,
                tokenId: decodedLog.tokenId,
                blockNumber:logs.blockNumber
            }   
            //console.log('rebuiltLogs',rebuiltLogs)
            if(!rebuiltLogs.error){
                //mint0
                if( web3.utils.hexToNumber(rebuiltLogs.from) == 0 ){                   

                    //txlogs
                    if (!logsObject[rebuiltLogs.txHash]){
                      txObjectValue.push(rebuiltLogs) 
                      //console.log('txObjectValue1',txObjectValue)                        
                    }
                    else{
                        txObjectValue = logsObject[rebuiltLogs.txHash]
                        txObjectValue.push(rebuiltLogs)  
                        //console.log('txObjectValue2',txObjectValue)                                        
                    }
                    Object.assign(logsObject,{[rebuiltLogs.txHash]:txObjectValue})
                    //console.log('logsObject',logsObject) 
                    //console.log('rebuiltLogs.blockHash',rebuiltLogs.blockHash)                          
                    if(rebuiltLogs.blockHash) bHash = rebuiltLogs.blockNumber
                    //console.log('bHash222',bHash)  
                }                     
            }     
        }        
        catch(e){
            //return {error : e}      
        }               
    })

    //console.log('logsObject2222222222',{ [bHash]:logsObject })    
    //console.log('bHash111',bHash)
    return bHash ? { [bHash]:logsObject } : {}
    
  }

  

