import * as React from 'react';
import PropTypes from 'prop-types';
import { Typography } from '@mui/material';
import { styled } from '@mui/material/styles';

import {useTranslation} from 'react-i18next';

import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';

import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

import TxDetail from './dialogs/TxDetail';
import MintRecord from './dialogs/MintRecord';
import MetaAttrDialog from './dialogs/MetaAttrDialog';

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
    minWidth:300
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
    minWidth:300
  },
}));

const BootstrapDialogTitle = (props) => {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[800],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
};

BootstrapDialogTitle.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func.isRequired,
};

export default function CommonDialogs(props) {
  const { t } = useTranslation()
  const [open, setOpen] = React.useState(false);
  // const [dialogObject, setDialog] = React.useState({
  //   type:'',
  //   msg:null
  // });     

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    //setOpen(false);
    props.closeDialog()//open
  };

  React.useEffect(() => {
    // setDialog({
    //   type:props.dialogType,
    //   msg:props.msg
    // })    
    //console.log('dialogObject==>',dialogObject)
    //console.log('props1111111111==>',props)    
    setOpen(props.isOpen)      
  }, [props]);

  // if (!props.warn) {
  //   return null;
  // }

  return (
    <div>
      <BootstrapDialog
        onClose={handleClose}
        aria-labelledby=""
        open={open}
      >
        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
          {props.dialogType == 'txDetail' && 
            <Typography variant="h3">
              {t('commonDialogs.txDetail')}
            </Typography> }
          {props.dialogType == 'mintRecord' && 
            <Typography variant="h3">
              {t('commonDialogs.mintRecord')}
            </Typography>
          }
          {props.dialogType == 'metaAttrDialog' && 
            <Typography variant="h3">
              {t('commonDialogs.metaAttrDialog')}
            </Typography>
          }
          
        </BootstrapDialogTitle>
          {props.dialogType == 'txDetail' && <TxDetail msg = { props.msg }></TxDetail>}
          {(props.dialogType == 'mintRecord') && <MintRecord msg = { props.msg }></MintRecord>}
          {(props.dialogType == 'metaAttrDialog') && <MetaAttrDialog action = {props.action}  msg = { props.msg }></MetaAttrDialog>}          
        <DialogActions>
        </DialogActions>
      </BootstrapDialog>
    </div>
  );
}
