import React, { useEffect, useState } from "react";
import {Grid} from '@mui/material';

// project imports
import BlockTransactions from "./BlockTransactions";

function BlockList(props) {
  const [newMintedDom, setNewMintedDom] = useState([])

  useEffect(() => {
    //console.log('props.data==>',props.data)       
    ParseMintedDom(props.data.newMintedDom)
  }, [props.data]); 
   
  const ParseMintedDom = ((blockListObject)=>{     
    let trans = []
    for (let i = 0; i< blockListObject.length;  i++) {
      if(blockListObject[i].blockTxs) {
      ////console.log('blockTxs[k]',transactionsOfBlock[k])      
      trans.push(
        <Grid item key={blockListObject[i].blockNumber} xs={12}  sx ={{ mt:1 }}>
          <Grid container>
            <Grid item  xs={12}>
            <BlockTransactions  
              data = {{
                'blockNumber' : blockListObject[i].blockNumber,
                'blockTxs' : blockListObject[i].blockTxs, 
                'followTx' : props.data.followTx, 
                'showDetail' : props.data.showDetail,
                'metamaskAction':props.data.metamaskAction
                }}
                />            
            </Grid> 
          </Grid>
        </Grid>
      )
    }}    
    setNewMintedDom(trans)
  })  

  return ( 
    <Grid container>
      <Grid item  xs={12}>
        {newMintedDom}
      </Grid>
    </Grid>
  );
}

export default React.memo(BlockList) 
