import React, { useState } from "react"

import { Grid, Button } from '@mui/material'
import osIcon from 'icons/images/opensea.svg'
import esIcon from 'icons/images/etherscan.svg'
import wsIcon from 'icons/images/website.svg'
import noIcon from 'icons/images/noPic.svg'
import detailIcon from 'icons/images/detail.svg'
import twitterIcon from 'icons/images/twitter.svg'

import { useTheme } from '@mui/material/styles'

const truncateLR = (input, lenL,lenR) =>
  input.length > lenL + lenR ? `${input.substring(0, lenL)}...${input.substring(input.length-lenR)}` : input;  

export default function SingleTransaction(props) {
  const theme = useTheme(); 
  const [transactionDetail, setTransactionDetail] = useState({
    txHash:'0x765f4f3d5372d029ea8bb4063b4b6ccf8987d03cfec6fbce3fef89970767bd2f',    
    contractName:'',
    contractAddress:'0xf27df5789016d5A34A8753fd715694f484C69d5A',
    contractAvater: noIcon,     
    contractMinted:0,       
    mintAmounts:0,
    price:0,
    method:'mint',
    gasPrice:32,
    gasUsed:200000,
    gasCostEther:0.001,
    gasCostDollor:2,
    website:'',
    osName:'',
    sameMint:1    
  });

  const [bg, setBg] = useState(theme.palette.grey[50])  

  React.useEffect(() => {
    //console.log('props.transactionDetail',props.transactionDetail)
    setTransactionDetail(props.transactionDetail)
  }, [props]);

  const changeOverBg = () =>{
    setBg(theme.palette.secondary.light)
  }
  
  const changeOutBg = () =>{
    setBg(theme.palette.grey[50])
  }

  const showDetail = (event) =>{
    event.preventDefault()
    props.showDetail(event)
  }

  const metamaskAction = (e) =>{
    //console.log(props)
    props.metamaskAction(e)
  }  


// 6

// flex-direction row | row-reverse | column | column-reverse
// flex-wrap flex-wrapnowrap | wrap | wrap-reverse;
// flex-flow flex-flowflex-directionflex-wraprow nowrap //flex-flow: <flex-direction> || <flex-wrap>;
// justify-content justify-contentjustify-content: flex-start | flex-end | center | space-between | space-around;
// align-items  align-items: flex-start | flex-end | center | baseline | stretch;
// align-content align-content: flex-start | flex-end | center | space-between | space-around | stretch;

// 6

// order order0
// flex-grow flex-grow0
// flex-shrink flex-shrink1
// flex-basis flex-basismain sizeauto
// flex flexflex-grow, flex-shrink  flex-basis0 1 auto
// align-self align-selfalign-itemsautoalign-itemsstretch

return (
  <Grid 
    container 
    style={{
      border:'solid 1px',
      borderColor: theme.palette.grey[300],
      alignItems:'center',
      color: theme.palette.grey[700],
      borderRadius:10
      }}
    >      
    <Grid 
      item 
      xs={12}
      style={{
        border:'solid 1px',
        borderColor: theme.palette.background.paper,
        backgroundColor:bg,
        borderRadius:10
      }}  
      onMouseOver = {changeOverBg}  
      onMouseOut = {changeOutBg}
    >
      <Grid container nowrap = 'true' spacing={1} style={{ margin: 5 }} display='flex'>

          <Grid item>
            <Grid container>
              <Grid item xs={12}>
                <img 
                  src={transactionDetail.contractAvater?transactionDetail.contractAvater:noIcon} 
                  alt="" 
                  style={{
                    width:32,
                    height:32,
                    margin:9,
                    borderRadius:50,
                    border:'solid 1px',
                    borderColor: theme.palette.grey[300]
                  }}
                />
              </Grid>
            </Grid>
          </Grid>

          <Grid item xs={12} sm={6}  md={6}>
            <Grid container>
              <Grid item xs={12}  sx={{pt:1,pb:1}}>                
                <Grid container style={{alignItems: 'flex-end'}}>

                  <Grid 
                    item 
                    sx={{
                      minWidth:40,
                      mr:1,
                      fontSize:15,
                      fontWeight:400,
                      color:theme.palette.grey[700]
                    }} >
                    <a nowrap = 'true'
                      href= {'https://opensea.io/collection/' + transactionDetail.osName}  
                      target="_blank" 
                      rel="noopener noreferrer"
                      style={{ 
                        textDecoration: 'none',
                        color: theme.palette.secondary.dark
                      }}
                    >
                    {`${transactionDetail.contractName}`}
                    </a>
                  </Grid> 

                  <Grid 
                    item  
                    sx={{
                    mr:1,
                    fontSize:14
                  }}>
                  <a 
                    href= {'https://etherscan.io/token/' + transactionDetail.contractAddress}  
                    target="_blank" rel="noopener noreferrer" 
                    style={{
                      textDecoration: 'none',
                      color:theme.palette.grey[700]
                    }}
                  >
                  {truncateLR(transactionDetail.contractAddress,4,4)}
                  </a>                              
                  </Grid> 
                <Grid 
                  item  
                  sx={{
                    p:0,
                    m:0,
                    mr:1,
                    pl:1,
                    pr:1,
                    fontSize:13,
                    fontWeight:400,
                    color:theme.palette.grey[700],
                    background:theme.palette.grey[300],
                    borderRadius:2,
                    textAlign: 'center'
                    }}
                  >
                  {`x${transactionDetail.mintAmounts}`}
                  </Grid>

                <Grid 
                  item  
                  sx={{
                    p:0,
                    m:0,
                    mr:1,
                    pl:1,
                    pr:1,
                    fontSize:13,
                    fontWeight:600,
                    color: theme.palette.background.paper,
                    background:theme.palette.primary.main,
                    borderRadius:50,
                    textAlign: 'center',
                    width:transactionDetail.sameMint * 30 < 300 ? transactionDetail.sameMint * 30 :300
                    }} 
                    title= {`Have ${transactionDetail.sameMint} same mints.`}
                  >                    
                  {`x${transactionDetail.sameMint}`}
                </Grid>                 
                </Grid>                
              </Grid>
              <Grid item xs={12}  sx={{pb:1}}>
                <Grid container sx={{fontSize:14}}>

                  <Grid item  sx={{mr:1,color: transactionDetail.price > 0 ? 'grey' : 'red'}}>          
                    {transactionDetail.price>0?parseFloat(transactionDetail.price).toFixed(6).replace(/[.]?0+$/g,""):'FREE'}                  
                  </Grid>

                  <Grid item  sx={{mr:1,fontStyle:'italic',fontSize:14,color:theme.palette.primary.dark}}>{transactionDetail.method}</Grid> 
                
                  <Grid item >
                    {`${transactionDetail.gasPrice}( ${transactionDetail.gasCostEther} )`}</Grid>
                </Grid> 
              </Grid>              
            </Grid>    
          </Grid>


          <Grid item xs={12} sm={2} md={2}>
            <Grid container>
              <Grid 
                item 
                xs={12} 
                sx={{
                  pt:1,
                  pb:1,
                  [theme.breakpoints.down('sm')]: {
                    pt:0                   
                  }
                }}>
                <Grid item container xs={12} > 

                  <Grid item sx={{mr:1}}>
                    <img 
                      role="presentation"
                      src={detailIcon} 
                      title={'transaction detail'} 
                      alt={transactionDetail.txHash} 
                      style={{width:16,cursor:'pointer'}} 
                      onClick={showDetail}                        
                    />                    
                  </Grid> 

                  <Grid item sx={{mr:1}}>
                    <a href= {'https://opensea.io/collection/'+ transactionDetail.osName}  target="_blank" rel="noopener noreferrer">
                      <img src={osIcon} alt="" style={{width:16}}/>
                    </a>  
                  </Grid>

                  <Grid item  sx={{mr:1}} >
                    <a href= {'https://etherscan.io/tx/'+transactionDetail.txHash}  target="_blank" rel="noopener noreferrer">
                      <img src={esIcon} alt="" style={{width:16}}/>
                    </a>                    
                  </Grid>  

                  {transactionDetail.website &&                  
                  <Grid item sx={{mr:1}}>                   
                    <a href={transactionDetail.website}  target="_blank" rel="noopener noreferrer">
                      <img src={wsIcon} title={transactionDetail.website} alt={''} style={{width:16}}/>
                    </a>
                  </Grid>
                  } 

                  {transactionDetail.twitter &&                  
                  <Grid item sx={{mr:1}}>                   
                    <a href={'https://twitter.com/'+ transactionDetail.twitter}  target="_blank" rel="noopener noreferrer">
                      <img src={twitterIcon} title={'https://twitter.com/'+ transactionDetail.twitter} alt={''} style={{width:16}}/>
                    </a>
                  </Grid>
                  }                   
                 
                </Grid>  
              </Grid>

              <Grid item xs={12} sx={{textAlign: 'left',fontSize:14,pb:1}}>
                {transactionDetail.contractMinted 
                  ? transactionDetail.contractMinted != 9999999
                   ? 'Minted ' + transactionDetail.contractMinted 
                   : ''
                  : ''                
                } 
                {transactionDetail.maxSupply!=996996 &&   '/'+transactionDetail.maxSupply}
              </Grid>  
              
            </Grid>            
          </Grid>          

          <Grid item  display='flex' sx={{flexGrow:1}}>
            <Grid container display='flex' alignItems={'left'} 
              sx={{
                pt:1.5,
                pb:1,
                [theme.breakpoints.down('sm')]: {
                  pt:0                   
                }
              }}
            >
              <Grid item>
                <Button 
                  variant="contained" 
                  title='mint by Private Key' 
                  value={transactionDetail.txHash} 
                  size="small" 
                  onClick={props.followTx}
                  color ="primary"
                  sx={{
                    flexShrink:1, 
                    my:0,  
                    mr: 0.5,                                          
                    color: theme.palette.secondary.dark, 
                    borderRadius: 12,
                    border: 'solid 0px',
                    borderColor: theme.palette.secondary[200],                    
                    backgroundColor:theme.palette.secondary.light,
                    '&:hover': {
                        background: theme.palette.secondary.dark,
                        color: theme.palette.secondary.light
                    }
                  }}   
                >
                  mint
                </Button>
              </Grid> 
              <Grid item>
                <Button 
                  variant="contained" 
                  title='mint by metamask'  
                  value={transactionDetail.txHash} 
                  size="small" 
                  onClick={metamaskAction} 
                  color ="primary"                 
                  sx={{
                    flexShrink:1, 
                    my:0,  
                    mr: 0.5,                                          
                    color: theme.palette.secondary.dark, 
                    borderRadius: 12,
                    border: 'solid 0px',
                    borderColor: theme.palette.secondary[200],                    
                    backgroundColor:theme.palette.secondary.light,
                    '&:hover': {
                        background: theme.palette.secondary.dark,
                        color: theme.palette.secondary.light
                    }
                  }}   
                >
                  metamask
                </Button>
              </Grid>                           
            </Grid>            
          </Grid>
      </Grid>
    </Grid>        
  </Grid>    
);
}

