import * as React from 'react';

import {useTranslation} from 'react-i18next';

import DialogContent from '@mui/material/DialogContent';
import {Typography,Grid} from '@mui/material';

import { useTheme } from '@mui/material/styles';

export default function MintRecord(props) {
  const { t } = useTranslation()
  const theme = useTheme()
  const [recordList, setRecordList] = React.useState([]);     

  React.useEffect(() => { 
    ParseRecordDom(props.msg)
  }, [props]);

  const ParseRecordDom = ((msg)=>{
    let recordDom = []
    if(msg.length == 0) return
    msg.forEach((rec,index) => {
      recordDom.push(
        <div key={index} style={{marginBottom:5,borderColor:theme.palette.grey[300],borderStyle:'solid',borderWidth:1,borderRadius:5,padding:5,}}>
          <Typography gutterBottom  style={{fontSize:14,color:theme.palette.grey[700]}} >
          <span style={{color:theme.palette.grey[900]}}>{'send: '}</span>{rec.from}
          </Typography>
          <Typography gutterBottom  style={{fontSize:14,color:theme.palette.grey[700]}} >
          <span style={{color:theme.palette.grey[900]}}>{'contract: '}</span>{rec.to}
          </Typography> 
          <Typography gutterBottom  style={{fontSize:14,color:theme.palette.grey[700]}} >
          <span style={{color:theme.palette.grey[900]}}>{'followedAddress: '}</span>{rec.followedAddress}
          </Typography>
          <Typography gutterBottom  style={{fontSize:14,color:theme.palette.grey[700]}} >
          <span style={{color:theme.palette.grey[900]}}>{'followedTx: '}</span>{rec.followedTx}
          </Typography> 
          <Typography gutterBottom  style={{fontSize:14,color:theme.palette.grey[700]}} >
          <span style={{color:theme.palette.grey[900]}}>{'statu: '}</span>
          <span style={{color:rec.statu == 'Successful' ? 'green':'red'}}>{rec.statu}</span>
          </Typography> 
          <Typography gutterBottom  style={{fontSize:14,color:theme.palette.grey[700]}} >
          <span style={{color:theme.palette.grey[900]}}>{'comment: '}</span>{rec.comment}
          </Typography> 
        </div>
        )
    })
    setRecordList(recordDom)
  })

  return (
        <DialogContent dividers>
          {recordList.length > 0 ? recordList : <Grid  style={{marginTop:20,marginBottom:20,fontSize:14,color:theme.palette.grey[700],textAlign:'center'}} >
          <span>{t('mintRecord.noData')}</span>
          </Grid> }               
        </DialogContent>
  );
}