  //check if can follow
  const checkCanFollow = (txHash) => {
    if(!(wAddr.addrArr.includes(transactionsObject[txHash].from))) return false
    if(transactionsObject[txHash].price > 0 && sOnlyFree) return false
    if(web3.utils.fromWei(transactionsObject[txHash].maxFeePerGas,'Gwei') > maxFeePerGas) return false
    if(!sOpenFollow) return false
    return true
  }
  
  const playBeep = () => {
    var audio  = new Audio(audioUrl);
    //audio.src = audioUrl.default 
    audio.play();
  }

  const checkInsertPosition = (blockNumber) => {
    if(blocknumberRecordArr.length > 0){     
      for(let i=0;i < blocknumberRecordArr.length;i++){
        //larger than last element
        if(blocknumberRecordArr[i] < blockNumber && i== 0) return 0
        //smaller than last element
        if(blocknumberRecordArr[i] > blockNumber && i== blocknumberRecordArr.length-1) return blocknumberRecordArr.length        
        if(blocknumberRecordArr[i] > blockNumber && blocknumberRecordArr[i+1] < blockNumber ) return i+1
      }
    }
    return 0
  }

  const ParseMintedDom = ((blockNumber,blockTxs)=>{
    ////////console.log('blockTxs==>',blockTxs)
    let trans = { blockNumber : blockNumber,blockTxs : blockTxs}
    //check the position the trans need to be put
    blocknumberRecordArr.length >= 6 && blocknumberRecordArr.pop()
    let insertPosition = checkInsertPosition(blockNumber)
    blocknumberRecordArr.splice(insertPosition, 0, blockNumber);
  
    //////console.log('insertPosition',insertPosition)
    //////console.log('blocknumberRecordArr',blocknumberRecordArr)    

    //////console.log('newMintedDom==>',newMintedDom)
    setNewMintedDom((prev) => {
      //////console.log('insertPosition',insertPosition)
      prev.length >= 6 && prev.pop()
      prev.splice(insertPosition, 0, trans)
      //console.log('iprev',prev)      
      return [...prev];
    })  

    isLoading = false
    //setNewMintedDom((prev) => [trans, ...prev])     

  })   

  const autoFollow = async (hash) =>{  
  //  ////console.log('transactionDetecteds[hash].logs',transactionDetecteds[hash].logs)  
    sOpenFollow && followTxAction(hash)
  }

