import * as React from 'react';
import {useTranslation} from 'react-i18next';

import DialogContent from '@mui/material/DialogContent';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';

export default function TxDetail(props) {
  const { t } = useTranslation()
  const [msg, setMsg] = React.useState('');    

  React.useEffect(() => {
    setMsg(props.msg); 
  }, [props]);

  return (
        <DialogContent dividers>
          <Typography gutterBottom  style={{color:'red'}} >
           {t('txDetail.hash')}
          </Typography>
          <Typography gutterBottom>
           {msg.hash}
          </Typography>
          
          <Typography gutterBottom  style={{color:'red'}} >
          {t('txDetail.from')}
          </Typography>
          <Typography gutterBottom>
           {msg.from}
          </Typography>
          
          <Typography gutterBottom  style={{color:'red'}} >
          {t('txDetail.contract')}
          </Typography>
          <Typography gutterBottom>
           {msg.to}
          </Typography>
          
          <Typography gutterBottom  style={{color:'red'}} >
           {t('txDetail.maxPriorityFeePerGas')}
          </Typography> 
          <Typography gutterBottom>
           {msg.maxPriorityFeePerGas/(10**9)}{' Gwei'}
          </Typography> 
                      
          <Typography gutterBottom  style={{color:'red'}}>
           {t('txDetail.maxFeePerGas')}
          </Typography>  
          <Typography gutterBottom>
           {msg.maxFeePerGas/(10**9)}{' Gwei'}
          </Typography>            

          <Typography gutterBottom  style={{color:'red'}}>
           {t('txDetail.value')}
          </Typography> 
          <Typography gutterBottom style={{maxWidth:200}}>
           {msg.value/(10**18)}{' Ether'}
          </Typography>  

          <Typography gutterBottom  style={{color:'red'}}>
           {t('txDetail.hexData')}
          </Typography> 
          <Typography gutterBottom>
            <div>
            <TextField
              fullWidth
              id="outlined-multiline-flexible"
              label=""
              multiline
              maxRows={10}
              value={msg.input}
            /> 
            </div> 
          </Typography>           
                           
        </DialogContent>
  );
}
