import * as React from 'react';
import {useTranslation} from 'react-i18next';

import DialogContent from '@mui/material/DialogContent';
import Typography from '@mui/material/Typography';
import {Grid,Button,TextField} from '@mui/material'

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';

const RowRadioButtonsGroup = (props) => {
  const [uploadedFiles, setUploadedFiles] = React.useState([])  
  let domArr = []
  if(props.radioGroup){
    props.radioGroup.forEach((el, i) => {
      domArr.push(
        <FormControlLabel value={ el['trait_type'] } id={props.radioLabel + '_' + i} key = {el['trait_type'] + '_' + i} control={<Radio />} label= { el['trait_type'] } />
      ) 
    })      
  }
  return (
    <FormControl>
      <FormLabel id="demo-row-radio-buttons-group-label">{props.radioLabel}</FormLabel>
      <RadioGroup
        row
        aria-labelledby="demo-row-radio-buttons-group-label"
        name={props.radioLabel}
        onChange = {props.action} 
      >
        { domArr }
      </RadioGroup>
    </FormControl>
  );
}

export default function MetaAttr(props) {
  const { t } = useTranslation()
  const [domArray, setDomArray] = React.useState([])
  const [radioValue, setRadioValue] = React.useState({})  

  const setInput = (e) => {
    console.log('e.target.name',e.target.name)    
    console.log('e.target.value',e.target.value)
    const n = e.target.name
    const v = e.target.value    
    setRadioValue((prev) => {
      let newValue = { ...prev, ...{[n]:v} }//
      console.log ('newValue',newValue)
      props.action(newValue)
      return newValue  
    })
  }

  const parseDomArray = ((obj) => {
    let domArr = []
    //console.log('obj:',obj);
    const attrKeys = Object.keys(obj)
    console.log('attrKeys:',attrKeys);    
    const attrValues = Object.values(obj) 
    console.log('attrValues:',attrValues);       
    attrKeys.forEach((element, i) => {
      domArr.push(
        <Grid item key={i} xs={3}>
          <RowRadioButtonsGroup 
            action = {setInput} 
            index = {i} 
            radioLabel = {element} 
            radioGroup = {attrValues[i]}
          />                      
        </Grid>
      ) 
    })
    //console.log("dom//////////",dom)
    setDomArray(domArr)
  })  

  React.useEffect(() => {
    parseDomArray(props.meta); 
  }, [props]);

  return (
   <>
     {domArray}
   </>
  );
}
