import React, { useEffect, useState } from "react";
import {Grid} from '@mui/material';

// project imports
import SingleTransaction from "./SingleTransaction";
import SubCard from 'ui-component/cards/SubCard'; 

export default function BlockTransactions(props) {
  const [newMintedDom, setNewMintedDom] = useState([])

  useEffect(() => {
    //console.log('props.data ==>',props.data)       
    ParseMintedDom(props.data)
  }, [props]);  

  const ParseMintedDom = ((mintedObject)=>{
    let trans = []
    //all txs
    const transactionsOfBlock = mintedObject.blockTxs
    //console.log('transactionsOfBlock',JSON.stringify(mintedObject.blockTxs)) 
    if(!transactionsOfBlock) return
    for (let k in transactionsOfBlock) {
    ////console.log('blockTxs[k]',transactionsOfBlock[k])      
      trans.push(
        <Grid item key={k} xs={12}>
          <Grid container>
            <Grid item  xs={12}>
             <SingleTransaction transactionDetail={transactionsOfBlock[k]} followTx={props.data.followTx}  showDetail={props.data.showDetail} metamaskAction={props.data.metamaskAction}/>            
            </Grid> 
          </Grid>
        </Grid>
      )
    }    
    setNewMintedDom(trans)
  })  

  return ( 
    <>
    {newMintedDom && 
      <SubCard title={props.data.blockNumber}>
        <Grid container direction="column" spacing={1}>
          {newMintedDom}
        </Grid>
      </SubCard>
    }         
    </>
  );
}
