import React, { useEffect, useState } from "react";
import {Grid,Button,TextField,Alert} from '@mui/material';
import { styled } from '@mui/material/styles';
import Storage from 'helper/Storage'

import {useTranslation} from 'react-i18next';

const Input = styled('input')({
  display: 'none',
});

export default function ImportAddress(props) {
  const { t } = useTranslation()
  const web3 = props.provider;

  const [addr, setAddr] = useState({
    addrStr:"",
    addrArr:[] 
   }); 
  
  const initAddr = ((addrObject)=>{
    setAddr(addrObject);
    props.setAddr(addrObject)
  })
  
  const buildAddr = ((addrStr)=>{
    let addrObject = {}
    const pattern = /0[xX]{1}[ABCDEFabcdef0-9]{40}/g;
    let addrArr = addrStr.match(pattern);
    addrObject.addrArr = addrArr ? addrArr : [];  
    addrObject.addrStr = addrArr.join('\r\n');  
    //console.log(addrObject)   
    setAddr(addrObject); 
    Storage.set('addr',addrObject)   
    props.setAddr(addrObject)     
  })

  const inputFile =()=> {
    let addrFile = document.querySelector('#addrFile').files[0];
    if(addrFile){
      var reader = new FileReader();
      reader.readAsText(addrFile);
      reader.onload = function(){ 
        ////console.log('this.result',this.result)                   
        buildAddr(this.result);
        document.querySelector('#addrFile').value=''//solute the same file can not be upload twice
      }
    }
  }  

  const setAddrAction = (event) => {
    buildAddr(event.target.value); 
  }; 

  // const pickPKs = () => {
  //   buildAddr(addr.addrArr.join('\r\n'))
  // };   
  
  const clearPKs =(()=>{
    try{
      let addrObject = {
        addrStr:"",
        addrArr:[]
      }
      setAddr(addrObject) 
      Storage.set('addr',addrObject)   
      props.setAddr([])         
      return true
    }
    catch(e){
      Alert("unknow error")
      return false
    }
  })

  useEffect(() => {
    const storageAddr = Storage.get('addr') 
    ? Storage.get('addr') 
     : {
      addrStr:"",
      addrArr:[] 
     }
     //console.log('storageAddr',storageAddr)
     initAddr(storageAddr);
	}, []);

  return (  
    <Grid container spacing={2}>      
      <Grid item xs={12}>
        <Grid container spacing={1}>
          <Grid item xs={9} >
            <TextField
            style = {{fontSize:8}}
            fullWidth
            id="pk"
            label={props.title + "(" + addr.addrArr.length + ")"}
            multiline
            rows={6}
            value={addr.addrStr}
            onChange={setAddrAction}
            disabled = {true} 
            error = {props.error}           
          />
          </Grid> 
          <Grid item xs={3}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
              <label htmlFor="addrFile">
                <Input accept="text/*" id="addrFile" multiple type="file" onChange={inputFile} />
                <Button variant="outlined" component="span">
                {t('importAddress.import')}
                </Button>
              </label>
              </Grid>                
              <Grid item xs={12}>
                <Button variant="outlined" onClick={clearPKs}  disabled = {addr.addrArr.length == 0}>{t('importAddress.clear')}</Button>
              </Grid>                                                       
            </Grid>
          </Grid>                          
        </Grid>
      </Grid>
    </Grid>    
  );
}
